.section {
  padding: 60px; }
  .section__header {
    font-size: 4.4em; }
    .section__header span {
      font-style: italic; }
  .section__sub-header {
    font-size: 2.0em;
    letter-spacing: .2em;
    text-transform: uppercase; }
  .section__content {
    margin-top: 20px;
    font-size: 1.0em;
    letter-spacing: 0.05em; }

@media (min-width: 768px) and (max-width: 1024px) {
  .section__header {
    font-size: 4.2em; }
  .section__sub-header {
    font-size: 2.0em;
    font-weight: normal; }
  .section__content {
    margin-top: 40px;
    font-size: 1.2em;
    font-weight: normal; } }

@media (min-width: 320px) and (max-width: 767px) {
  .section {
    padding: 20px; }
    .section__header {
      font-size: 4.0em; }
    .section__sub-header {
      font-size: 1.2em;
      letter-spacing: 0.1em; }
    .section__content {
      font-size: 1.0em; } }



